import React from 'react'
import { Link } from "react-router-dom";

function Courses() {
    return (
        <>
            <h1 className="center">Courses</h1>

            <h2 className="center">List of courses</h2>

            <br/>
            <Link to="courses/C-programming">
                <button>C programming</button>
            </Link>
            <br/>
            <br/>
            <Link to="courses/Cpp-programming">
                <button>C++ programming</button>
            </Link>
        </>
    )
}

export default Courses
